<template>
    <div>
        <navbar title="公告详情" v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <div v-html="notice.content"></div>
        </div>
    </div>
</template>

<script>
    import {lookNotice} from "../../api/center";
    import { toast, Dialog } from 'vant';
    import navbar from "@/components/navbar";
    export default {
        name: "Noticedetail",
        components: {
            navbar
        },
        data()
        {
            return {
                notice_id:'',
                notice:{}
            }
        },
        mounted()
        {
            this.notice_id = this.$route.query.noticeId
            this.lookup()

        },
        methods:{
            lookup()
            {
                lookNotice({notice_id:this.notice_id}).then(res => {
                    this.notice = res
                })
            },

        }
    }
</script>

<style scoped>
    @import "../../static/css/notice.css";
    .app
    {
        color: #333333;
        font-size: .14rem;
    }
</style>



